<template>
  <a-spin :spinning="loading" tip="loading">
    <div
      :style="{
        top: `${offset.top}px`,
        left: `${offset.left}px`
      }"
      class="dashboard-container"
    >
      <div
        :style="{
          ...dashboard.container.style,
          width: `${dashboard.container.layout.width}px`,
          height: `${dashboard.container.layout.height}px`,
          backgroundImage: dashboard.container.style.backgroundImage
            ? `url(${dashboard.container.style.backgroundImage})`
            : generateBackgroundGradient(
                dashboard.container.style.backgroundGradient
              ),
          transform: `scale(${scale})`
        }"
        class="dashboard"
      >
        <LoopPanels
          :api-collection="dashboard.apiCollection"
          :list="dashboard.list"
          :container="dashboard.container"
        />
      </div>
    </div>
  </a-spin>
</template>

<script>
// components
import LoopPanels from './components/LoopPanels'
// apis
import { getDashboardById } from '@/api/v1/dashboard'
import { getDashboardByToken } from '@/api/v1/dashboard/token'
// utils
import {
  bindEvent,
  removeEvent,
  debounce,
  deepClone,
  generateBackgroundGradient
} from '@/utils/methods'
// utils
import request from '@/utils/requestByMode'
// constants
import templates from '@/lib/templates'
export default {
  components: {
    LoopPanels
  },
  data() {
    return {
      bodyRect: 0,
      constants: {
        templates
      },
      loading: false,
      offset: {
        top: 0,
        left: 0
      },
      dashboard: {
        container: {
          layout: {},
          style: {}
        },
        list: [],
        animationList: []
      }
    }
  },
  computed: {
    scale() {
      const { width, height } = this.dashboard.container.layout
      const scaleWidth = this.bodyRect.width / width
      const scaleHeight = this.bodyRect.height / height
      const scale = Math.min(scaleWidth, scaleHeight)
      this.setOffset(scale || 1, width, height)
      return scale || 1
    }
  },
  async mounted() {
    const _this = this
    const { id, token, template, preview } = this.$route.query
    this.setBodyRect()
    if (id) {
      await this.initDashboardById(id)
    } else if (token) {
      await this.initDashboardByToken(token)
    } else if (template) {
      this.initDashboardByTemplate(template)
    } else if (preview) {
      this.initDashboardByStorage()
    }
    this.$nextTick(() => {
      this.animate('mounted')
    })
    bindEvent(window, 'resize', debounce(this.setBodyRect, 300))
    bindEvent(window, 'beforeRouteLeave', function({ detail: { callback } }) {
      _this.animate('beforeRouteLeave', callback)
    })
  },
  beforeDestroy() {
    clearTimeout(this.animateTimer)
    removeEvent(window, 'resize', debounce(this.setBodyRect, 300))
  },
  methods: {
    generateBackgroundGradient,
    setBodyRect() {
      this.bodyRect = document.documentElement.getClientRects()[0]
    },
    async initDashboardById(id) {
      this.loading = true
      await getDashboardById(id).then(
        res => {
          if (res.code === 0) {
            const config = res.data?.config ? JSON.parse(res.data.config) : {}
            this.requestCollection(config.apiCollection, () => {
              this.loading = false
              this.dashboard = {
                ...config,
                title: res.data.title
              }
            })
          } else {
            this.loading = false
            this.$message.error(res.desc)
          }
        },
        () => {
          this.loading = false
        }
      )
    },
    async initDashboardByToken(token) {
      this.loading = true
      await getDashboardByToken(token).then(
        res => {
          this.loading = false
          if (res.code === 0) {
            const { title, config } = res.data
            this.dashboard = {
              ...JSON.parse(config),
              title
            }
          } else {
            this.$message.error(res.desc)
          }
        },
        () => {
          this.loading = false
        }
      )
    },
    initDashboardByTemplate(template) {
      const { title, config } = this.constants.templates[template]
      this.dashboard = {
        ...config,
        title
      }
    },
    initDashboardByStorage() {
      const storage = sessionStorage.getItem('dashboard')
      this.dashboard = JSON.parse(storage)
    },
    requestCollection(apis = [], callback) {
      Promise.all(
        apis.map((item, index) => {
          request(
            item,
            res => {
              const collection = {
                ...item,
                data: deepClone(res)
              }
              apis.splice(index, 1, collection)
            },
            err => {
              this.$message.error('Network Error')
            }
          )
          if (item.interval) {
            this.intervalGetAsyncData(item.interval, item.api, () =>
              request(
                item,
                res => {
                  const collection = {
                    ...item,
                    data: deepClone(res)
                  }
                  apis.splice(index, 1, collection)
                },
                err => {
                  this.$message.error('Network Error')
                }
              )
            )
          }
        })
      ).then(res => {
        callback && callback()
      })
    },
    intervalGetAsyncData(interval, timer, fn) {
      clearInterval(this[timer])
      if (Number(interval)) {
        this[timer] = setInterval(fn, interval * 1000)
      }
    },
    setOffset(scale, width, height) {
      this.offset.top = (this.bodyRect.height - height * scale) / 2
      this.offset.left = (this.bodyRect.width - width * scale) / 2
    },
    animate(timeNode, callback) {
      this.dashboard.animationList
        .filter(item => item.animation.timeNode === timeNode)
        .forEach(item => {
          const {
            id,
            animation: { type, value, delay, speed }
          } = item
          const target = this.$el.querySelector(`#animate-${id}`)
          if (target) {
            if (type === 'in') {
              target.style.opacity = 0
            } else {
              target.style.opacity = 1
            }
            this.animateTimer = setTimeout(() => {
              target.className = `animate animate__animated animate__${value}`
              target.style.animationDuration = `${speed}s`
              target.style.opacity = 1
            }, delay * 1000)
          }
        })
      const generatedArr = this.dashboard.animationList
        .filter(item => item.animation.timeNode === timeNode)
        .map(item => item.animation.delay + item.animation.speed)
      const maxDelay = Math.max(...generatedArr)
      callback &&
        setTimeout(() => {
          callback()
        }, (maxDelay / 2) * 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.dashboard-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  .dashboard {
    position: relative;
    background-position: center center;
    background-size: 100%;
    transform-origin: 0 0;
    overflow: hidden;
  }
}
</style>
