<template>
  <div>
    <div
      v-for="(item, index) in list"
      v-show="item.visible"
      :id="item.id"
      :key="item.id"
      :style="{
        zIndex: index,
        width: item.layout.width ? `${item.layout.width}px` : 'auto',
        height: item.layout.height ? `${item.layout.height}px` : 'auto',
        marginTop: `${parent ? -parent.layout.y : 0}px`,
        marginLeft: `${parent ? -parent.layout.x : 0}px`,
        transform: `translate(${item.layout.x}px, ${item.layout.y}px)`
      }"
      class="panel-wrapper"
    >
      <div :id="`animate-${item.id}`" class="animate">
        <LoopPanels
          v-if="item.isRoot"
          :parent="item"
          :api-collection="apiCollection"
          :list="item.children"
          :container="container"
        />
        <Panel
          v-else
          :data-source="item"
          :api-collection="apiCollection"
          :container="container"
          :play="true"
        />
      </div>
    </div>
  </div>
</template>

<script>
// components
import Panel from '@/components/Panel'
export default {
  name: 'LoopPanels',
  components: {
    Panel
  },
  props: {
    parent: {
      type: Object
    },
    apiCollection: {
      type: Array,
      required: true,
      default: () => []
    },
    list: {
      type: Array,
      required: true
    },
    container: {
      type: Object,
      required: true
    }
  }
}
</script>

<style lang="less" scoped>
.panel-wrapper {
  position: absolute;

  .animate {
    width: 100%;
    height: 100%;
  }
}
</style>
